body {
  position: relative;
}

.speckle-demo {
  background-color: #e7e7e7;
  height: 300px;
  margin: 80px auto 500px;
  position: relative;
  width: 600px;
  
  h3 {
    background-color: #999999;
    border-radius: 20px;
    color: #ffffff;
    display: inline-block;
    font-family: sans-serif;
    line-height: 1;
    margin: 15px;
    padding: 10px 15px;
    position: relative;
    z-index: 9999;
  }

}